<template>
  <div class="">
    <main-layout>
      <main-header slot="header"></main-header>
      <div class="home-main" slot="body-main">
        <div id="homeBodyId" class="home-body"></div>
      </div>
      <main-menu slot="body-left"></main-menu>
      <main-footer slot="footer"></main-footer>
    </main-layout>
  </div>
</template>

<script>
import echarts from 'echarts'
import homeJson from '@/json/home/Home.js'
export default {
  name: 'Home',
  data () {
    return {

    }
  },
  mounted(){
    console.log("homeJson:",homeJson.homeData);
    this.loadMap();
  },
  methods: {
    loadMap(){
      let myChart = echarts.init(document.getElementById('homeBodyId')); 
      var option = {
            backgroundColor: "#02AFDB",
            title: {
              show:"true",//æ¯å¦æ¾ç¤ºæ é¢ï¼é»è®¤æ¾ç¤ºï¼å¯ä»¥ä¸è®¾ç½®
              text:"ä¸çéè¿¹åå¸å¾",//å¾è¡¨æ é¢ææ¬åå®¹
              left: 'center',
              top: 'top'
            },
            tooltip: {
              trigger: 'item',
              formatter: function (params) {
                console.log("params:", params);
                // console.log("params-data:", params.data);
                var value = (params.value + '').split('.');
                value = value[0].replace(/(\d{1,3})(?=(?:\d{3})+(?!\d))/g, '$1,')+ '.' + value[1];
                return params.seriesName + 
                      '<br/>' + 
                      params.name +
                      ' : ' + 
                      value;
              }  
            }, 
            dataRange: {
              x: 'left',
              y: 'bottom',
              splitList: [
                {start: 11, color: '#87481f'},
                {start: 9, end: 10, color: '#b76f40'},
                {start: 4, end: 8, color: '#b69968'},
                {start: 3, end: 3, color: '#b7ba6b'},
                {start: 2, end: 2, color: '#40835e'},
                {start: 1, end: 1, color: '#bed742'},
                {end: 0, end: 1, color: '#fff'}
              ]
           },   
            // visualMap: {
            //   min: 0,
            //   max: 10,
            //   text:['High','Low'],
            //   realtime: false,// ææ½æ¶ï¼æ¯å¦å®æ¶æ´æ°
            //   calculable: true,// æ¯å¦æ¾ç¤ºææ½ç¨çææ
            //   color: ['orangered','yellow','#a3cf62']
            // },
            series: [{
              name: 'World Heritage (2021)',
              type: 'map',
              mapType: 'world',
              roam: true,// æ¯å¦å¼å¯é¼ æ ç¼©æ¾åå¹³ç§»æ¼«æ¸¸ é»è®¤ä¸å¼å¯ å¦æåªæ³è¦å¼å¯ç¼©æ¾æèå¹³ç§»ï¼å¯ä»¥è®¾ç½®æ 'scale' æè 'move' è®¾ç½®æ true ä¸ºé½å¼å¯
              itemStyle:{
                emphasis:{label:{show:true}}
              },
              data: homeJson.homeData
            }]
          };
          myChart.setOption(option);
      }
  }
}
</script>

<style scoped>
.home-main{
    width: 100%;
    height: 100%;
}

.home-main .home-body{
    /* background-color: #80d6ff; */
    width: 100%;
    height: 100%;
}
</style>



