import { getRequest, postRequest } from "../axios/new-axios"

export const addword = (params) => {
    console.log("wordService-addword-requestParams:", params);
    return postRequest('/word/addword', params)
}

export const getallword = () =>{
    return getRequest('/word/getallword')
}


