<template>
  <div class="">
    <main-layout>
      <main-header slot="header"></main-header>
      <div class="kana-main" slot="body-main">
        <div class="open-btn-div">
          <div class="btn-size" @click="openAddWord()">æ·»å è¯æ±</div>
        </div>

        <div class="list-div">
          <el-collapse v-model="activeNames">
            <div class="list-obj-div" v-for="(word, index) in wordList">
              <el-collapse-item :title=word.nWord :name=word.id>
                <div class="show-div-size">
                  <div>{{word.lWord}}</div>
                  <div>
                    <p>{{word.cWord}}  [{{word.wClass}}]      {{word.jWord}}</p>
                  </div>
                  <div>
                    {{word.createTime}}
                    <div class="updata-btn-div">
                      <div class="btn-min-size el-icon-edit" @click="openUpdateWord(word)"></div>
                    </div>
                  </div>
                </div>
              </el-collapse-item>
            </div>
          </el-collapse>
        </div>
        
        <!-- 
          el-drawer: Draweræ½å±
          direction: Drawer æå¼çæ¹åï¼rtl / ltr / ttb / bttï¼
        -->
        <el-drawer 
          :title=drawerTitle
          :visible.sync="dialog"
          direction="rtl"
          size="30%">
            <div class="add-main">
              <div class="add-div-tr">
                <span>æ±å­ï¼</span>
                <span>
                  <input type="text" class="add-input" v-model="cWord"/>
                </span>
              </div>
              <div class="add-div-tr">
                <span>æ¥æï¼</span>
                <span>
                  <input type="text" class="add-input" v-model="jWord"/>
                </span>
              </div>
              <br><br>
              <div class="add-div-tr">
                <span>ç½é©¬é³ï¼</span>
                <span>
                  <input type="text" class="add-input" v-model="lWord"/>
                </span>
              </div>
              <div class="add-div-tr">
                <span>æ³¨è¯ï¼</span>
                <span>
                  <input type="text" class="add-input" v-model="nWord"/>
                </span>
              </div>
              <br><br>
              <div class="add-div-tr">
                <span>è¯ç±»ï¼</span>
                <span>
                  <input type="text" class="add-input" v-model="wClass"/>
                </span>
              </div>
              <br><br>
              <div class="add-btn-div">
                <div class="btn-size" @click="addWord()" v-show="isShowAddBtn">æäº¤</div>
                <div class="btn-size" @click="updateWord()" v-show="isShowUpdateBtn">æ´æ°</div>
              </div>
              <div class="div-message">
                <span class="font-red">{{message}}</span>
              </div>
            </div>
        </el-drawer>
      </div>
      <main-menu slot="body-left"></main-menu>
      <main-footer slot="footer"></main-footer>
    </main-layout>
  </div>
</template>

<script>
import { addword, getallword } from '@/service/wordService.js'
export default {
  name: 'Word',
  data () {
    return {
      dialog: false,
      cWord: null,
      jWord: null,
      lWord: null,
      nWord: null,
      wClass: null,
      message: "",
      activeNames: null,
      wordList: [],
      drawerTitle: "",
      isShowAddBtn: true,
      isShowUpdateBtn: false
    }
  },
  methods: {
    /**
     * æå¼æ·»å è¯æ±ç»é¢
    */
    openAddWord(){
      this.drawerTitle = "æ·»å è¯æ±"
      this.dialog = true
      this.isShowAddBtn = true
      this.isShowUpdateBtn = false
    },
    /**
     * æ·»å è¯æ±
    */
    addWord(){
      //è¯·æ±åæ°
      let paramValue = {};
      paramValue.cWord = this.cWord
      paramValue.jWord = this.jWord
      paramValue.lWord = this.lWord
      paramValue.nWord = this.nWord
      paramValue.wClass = this.wClass

      addword(paramValue).then(res => {
        console.log("addword-response:" + JSON.stringify(res));
        let resData = res.data
        
        if(resData.success){
          this.cWord = null
          this.jWord = null
          this.lWord = null
          this.nWord = null
          this.wClass = null
          this.message = resData.code + ":" + resData.message
        }else{
          this.message = resData.code + ":" + resData.message
        }
      })
    },
    /**
     * è·åå¨é¨è¯æ±
    */
    wordListInit(){
      getallword().then(res => {
        console.log("getallword-response:" + JSON.stringify(res));
        this.wordList = []
        let resData = res.data

        if(resData.success){
          this.wordList = resData.data
        }
      })
    },
    /**
     * æå¼æ´æ°è¯æ±ç»é¢
    */
    openUpdateWord(wordObj){
      this.cWord = wordObj.cWord
      this.jWord = wordObj.jWord
      this.lWord = wordObj.lWord
      this.nWord = wordObj.nWord
      this.wClass = wordObj.wClass
      this.drawerTitle = "æ´æ°è¯æ±"
      this.dialog = true
      this.isShowAddBtn = false
      this.isShowUpdateBtn = true
    }
  },
  created(){
    this.wordListInit();
  }
}
</script>

<style scoped>
.add-main .add-div-tr{
  margin-top: 20px;
  margin-left: 20px;
  width: 230px;
  display: inline;
}
.add-main .add-div-tr .add-input{
  width: 100px;
  /*
    å»é¤æªéä¸­ç¶æè¾¹æ¡
  */
  border: 0;
  border-bottom: 1px solid black;
  /*
    å»é¤éä¸­ç¶æçè¾¹æ¡
  */
  outline: none;
}

.kana-main .list-div{
  margin-left: 15px;
  margin-top: 10px;
}

.list-div .list-obj-div .show-div-size .updata-btn-div{
  float: right;
  width: 110px;
}

.add-btn-div{
  margin-left: 140px;
}
.open-btn-div{
  margin-left: 10px;
  margin-top: 10px;
}
.btn-size{
  background: rgb(64, 142, 194);
  border: 1px solid rgb(64, 142, 194);
  cursor: pointer;
  color: #ffff;
  -webkit-appearance: none;
  text-align: center;
  outline: 0;
  margin: 0;
  font-weight: 500;
  font-size: 14px;
  border-radius: 4px;
  max-width: 160px;
  height: 26px;
  padding-top: 6px;
}
.btn-min-size{
  cursor: pointer;
  -webkit-appearance: none;
  text-align: center;
  outline: 0;
  margin: 0;
  font-weight: 500;
  font-size: 14px;
  border-radius: 4px;
  width: 30px;
  height: 20px;
  padding-top: 6px;
  color: black;
}

.div-message{
  text-align: center;
  margin-top: 20px;
}
.div-message .font-red {
  color: rgb(196, 5, 5);
}
</style>



